  from {
    transform: translate3d(0, 0, 0);
    opacity: 1; }
  to {
    transform: translate3d(0, -50%, 0);
    opacity: 0; } }

@keyframes modalFadeInLeft {
  from {
    transform: translate3d(-100%, 0, 0);
    opacity: 0; }
  to {
    transform: translate3d(0, 0, 0);
    opacity: 1; } }

