  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  background-color: black;
  z-index: 1;
  overflow: hidden; }

.Slides {
  display: flex;
  height: calc(100% - 120px);
  width: 100%;
  z-index: 1; }
  .Slides [class^="react-transform-wrapper"] {
    flex-shrink: 0;
    width: 100%;
    height: 100%; }
  .Slides [class^="react-transform-content"] {
    width: 100%;
    height: 100%; }
  .Slides [class^="react-transform-component"] {
    width: 100%;
    height: 100%; }
    .Slides [class^="react-transform-component"] img {
      height: 100%; }
  .Slides .carousel-arrow-left {
    display: block;
    position: absolute;
    z-index: 4;
    top: 50%; }
    @media only screen and (max-width: 574px) {
      .Slides .carousel-arrow-left {
        display: none; } }
  .Slides .carousel-arrow-right {
    display: block;
    position: absolute;
    z-index: 4;
    top: 50%;
    right: 0; }
